<template>
  <v-app-bar color="primary" dense dark app height="60">
    <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->

    <v-toolbar-title style="font-weight: bold;">{{ links[value]?.title }}</v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn icon @click="changeTheme">
      <v-icon v-if="dark">mdi-white-balance-sunny</v-icon>
      <v-icon v-else>mdi-weather-night</v-icon>
    </v-btn>

    <v-btn icon @click="signout">
      <v-icon>mdi-logout-variant</v-icon>
    </v-btn>

    <template v-slot:extension>
      <v-tabs centered>
        <v-tabs-slider color="yellow"></v-tabs-slider>
        <v-tab v-for="(link, i) in links" :key="i" 
          active-class="white--text" class="tw"
          link :to="link.url" @click="value=i">
          <v-icon>{{ link.icon }}</v-icon>
          <!-- <span>{{ link.title }}</span> -->
        </v-tab>
      </v-tabs>
    </template>
  </v-app-bar>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: "AppBar",
  data: () => ({
    value: 0,
    links: [
      { title: "Товары", icon: "mdi-home", url: "/" },
      { title: "Клиенты", icon: "mdi-account", url: "/users" },
      { title: "Приходы", icon: "mdi-cart-arrow-down", url: "/parishes" },
      { title: "Расходы", icon: "mdi-cart-arrow-up", url: "/expenses" },
      { title: "Прибыль", icon: "mdi-hand-coin", url: "/profit" },
    ],
  }),
  computed: mapGetters(['dark']),
  methods: {
    ...mapMutations(['logout', 'settheme', 'openAlert']),
    signout(){
      this.$router.push('/login')
      this.logout()
    },
    changeTheme(){
      this.settheme()
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark
    },
  },
  created(){
    this.value = this.links.findIndex(l => '#'+l.url === location.hash)
  }
};
</script>

<style scoped>
.tw {
  min-width: 70px !important;
  width: 100% !important;
  max-width: 90px !important;
}
</style>