<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      class="elevation-1"
      :items="products" :loading="loading"
      :search="search">
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="12" sm="5" md="5">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                hide-details
                dense outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" md="4" class="d-flex justify-end">
              <!-- <v-spacer></v-spacer> -->
              <v-btn block color="primary" @click="dialog = true"
                >Добавить Товар</v-btn>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Изменить</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Удалить</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
      <!-- <template v-slot:item.type="{ item }">
        {{ item.type === 'Diaper' ? 'Памперс' : item.type === 'Soap' ? 'Мыло' : 'Влажные салфетки' }}
      </template> -->
    </v-data-table>
    <v-dialog v-model="dialog" max-width="400px">
      <v-card>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-card-title class="mt-2">
            <span class="text-h5 ml-3">{{ formTitle }}</span>
          </v-card-title>
  
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.name"
                    label="Название товара"
                    hide-details
                    outlined :rules="nameRules"
                  >
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-select
                    v-model="editedItem.type"
                    hide-details :items="['Diaper','Soap','Wipes']"
                    label="Тип товара"
                    outlined :rules="nameRules"
                    >
                    <!-- :items="types" item-text="text"
                    item-value="value" -->
                  </v-select>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.balance"
                    label="Остаток товара"
                    hide-details
                    outlined :rules="numberRules"
                    type="number"
                    min="0"
                  >
                  </v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
  
          <v-card-actions class="px-7">
            <v-spacer></v-spacer>
            <v-btn color="primary" text @click="close"> Отмена </v-btn>
            <v-btn color="primary" :disabled="!valid" text @click="validate">
              Сохранить
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDelete" max-width="420px">
      <v-card outlined tile>
        <v-card-title class="text-h5">
          Вы уверены, что хотите удалить этот элемент?
        </v-card-title>
        <v-card-text
          >Внимание. После того, как информация будет удалена, она не может быть
          восстановлена!
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="closeDelete">Отмена </v-btn>
          <v-btn color="primary" text @click="deleteItemConfirm"
            >Удалить
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: "Products",
  data: () => ({
    search: "",
    products: [],
    dialog: false,
    dialogDelete: false,
    loading: false,
    headers: [
      { text: "Название товара", value: "name", align: 'start' },
      { text: "Тип товара", value: "type" },
      { text: "Остаток товара", value: "balance" },
      // { text: "Дата", value: "date" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    editedIndex: -1,
    editedId: null,
    valid: false,
    image: null,
    editedItem: {
      name: "",
      type: "",
      balance: 0,
    },
    defaultItem: {
      name: "",
      type: "",
      balance: 0,
    },
    types: [
      {text: 'Памперс', value: 'Diaper'},
      {text: 'Мыло', value: 'Soap'},
      {text: 'Влажные салфетки', value: 'Wipes'},
    ],
    nameRules: [(v) => !!v || "Укажите поля"],
    numberRules: [(v) => !!v?.toString()?.trim() || "Укажите поля"],
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Добавить Товар" : "Редактировать Товар";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    ...mapMutations(['openAlert']),
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    async initialize() {
      this.loading = true
      const { data } = await this.$http.get('/api/v1/products/')
      this.products = data
      this.loading = false
    },

    editItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.editedId = item.id;
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.editedId = item.id;
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      this.dialogDelete = false;
      this.products.splice(this.editedIndex, 1);
      await this.$http.delete(`/api/v1/products/${this.editedId}`)
      this.openAlert({status: true, text: 'Успешно удалено!'})
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.image = null;
        this.$refs.form?.reset();
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.image = null;
        this.$refs.form?.reset();
      });
    },

    async save() {
      try{
        if (this.editedIndex > -1) {
          const { data } = await this.$http.put(`/api/v1/products/${this.editedId}`, this.editedItem)
          Object.assign(this.products[this.editedIndex], data);
          this.openAlert({status: true, text: 'Успешно изменено!'})
        } else {
          const { data } = await this.$http.post('/api/v1/products/', this.editedItem)
          this.products.push(data);
          this.openAlert({status: true, text: 'Успешно добавлено!'})
        }
        this.close();
      }catch(err){
        console.log(err);
        this.openAlert({status: false, text: 'Ошибка!'})
      }
    },
  },
};
</script>