<template>
    <v-container fluid>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :loading="loading"
        class="elevation-1"
      >
        <template v-slot:top>
          <div class="d-flex py-3 px-3">
            <v-row class="d-flex justify-space-between">
              <v-col cols="12" sm="5" md="5">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Поиск"
                  hide-details
                  dense
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" md="4" class="d-flex justify-end">
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="400px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn block class="mb-2" v-bind="attrs" v-on="on" color="primary">
                      Добавить прибыль
                    </v-btn>
                  </template>
                  <v-card>
                    <v-form ref="form" v-model="valid" lazy-validation>
                      <v-card-title>
                        <span class="text-h5 ml-3">{{ formTitle }}</span>
                      </v-card-title>
  
                      <v-card-text class="pb-0">
                        <v-container>
                          <v-row>
                            <v-col cols="12">
                                <v-select
                                v-model="editedItem.client"
                                label="Клиент"
                                :items="clients"
                                outlined
                                item-text="name"
                                item-value="id"
                                hide-details
                                :rules="nameRules"
                                >
                                </v-select>
                            </v-col>
                            <v-col cols="12">
                              <v-text-field
                                v-model="editedItem.profit"
                                label="Сумма прибыли"
                                outlined
                                hide-details
                                type="number"
                                min="0"
                                :rules="numberRules"
                              >
                              </v-text-field>
                            </v-col>
                          </v-row>
                        </v-container>
                      </v-card-text>
  
                      <v-card-actions class="pr-6">
                        <v-spacer></v-spacer>
                        <v-btn color="primary" text @click="close">
                          Отмена
                        </v-btn>
                        <v-btn color="primary"
                          :disabled="!valid"
                          text @click="validate"
                        >
                          Сохранить
                        </v-btn>
                      </v-card-actions>
                    </v-form>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="dialogDelete" max-width="420px">
                  <v-card outlined tile>
                    <v-card-title class="text-h5">
                      Вы уверены, что хотите удалить этот элемент?
                    </v-card-title>
                    <v-card-text
                      >Внимание. После того, как информация будет удалена, она не
                      может быть восстановлена!
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="primary" text @click="closeDelete"
                        >Отмена
                      </v-btn>
                      <v-btn color="primary" text @click="deleteItemConfirm"
                        >Удалить
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-col>
            </v-row>
          </div>
        </template>
        <template v-slot:item.client="{ item }">
          {{ item.client?.name }}
        </template>
        <template v-slot:item.actions="{ item }">
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" icon>
                <v-icon>mdi-dots-horizontal</v-icon>
              </v-btn>
            </template>
            <v-list dense>
              <v-list-item link @click="editItem(item)">
                <v-list-item-title>Изменить</v-list-item-title>
              </v-list-item>
              <v-list-item link @click="deleteItem(item)">
                <v-list-item-title>Удалить</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </template>
      </v-data-table>
    </v-container>
  </template>
  
  <script>
  import { mapMutations } from 'vuex'

  export default {
    name: "Users",
    data: () => ({
      search: "",
      list: [],
      dialog: false,
      dialogDelete: false,
      loading: false,
      headers: [
        { text: "Имя клиента", value: "client", sortable: false, align: "start" },
        { text: "Сумма прибыли", value: "profit" },
        { text: "Дата прибыли", value: "created_at" },
        { text: "Действия", value: "actions", sortable: false },
      ],
      editedIndex: -1,
      editedId: null,
      valid: false,
      editedItem: {
        client: 0,
        profit: "",
      },
      defaultItem: {
        client: 0,
        profit: "",
      },
      clients: [],
      nameRules: [(v) => !!v || "Укажите поля"],
      numberRules: [(v) => !!v?.toString()?.trim() || "Укажите поля"],
    }),
    computed: {
      formTitle() {
        return this.editedIndex === -1
          ? "Добавить прибыл"
          : "Редактировать прибыл";
      },
      filteredItems () {
        return this.list.filter(item => {
          return item.client.name.toLowerCase().includes(this.search.toLowerCase()) 
            || String(item.profit).toLowerCase().includes(this.search.toLowerCase())
            || String(item.created_at).toLowerCase().includes(this.search.toLowerCase())
        })
      }
    },
  
    watch: {
      dialog(val) {
        val || this.close();
      },
      dialogDelete(val) {
        val || this.closeDelete();
      },
    },
    created() {
      this.initialize();
    },
    methods: {
    ...mapMutations(['openAlert']),
      validate() {
        if (this.$refs.form.validate()) {
          this.save();
        }
      },
      async initialize() {
        this.loading = true
        const { data } = await this.$http.get('/api/v1/profits/')
        this.loading = false
        this.list = data;
        const clients = await this.$http.get('/api/v1/clients/')
        this.clients = clients.data
      },

      editItem(item) {
        this.editedIndex = this.list.indexOf(item);
        this.editedItem = Object.assign({}, item);
        this.editedId = item.id;
        this.dialog = true;
      },
  
      deleteItem(item) {
        this.editedIndex = this.list.indexOf(item);
        this.editedItem = Object.assign({}, item);
        this.editedId = item.id;
        this.dialogDelete = true;
      },
  
      async deleteItemConfirm() {
        this.list.splice(this.editedIndex, 1);
        await this.$http.delete(`/api/v1/profits/${this.editedId}`)
        this.openAlert({status: true, text: 'Успешно удалено!'})
        this.closeDelete();
      },
  
      close() {
        this.dialog = false;
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem);
          this.editedIndex = -1;
          this.editedId = null;
          this.$refs.form?.reset();
        });
      },
  
      closeDelete() {
        this.dialogDelete = false;
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem);
          this.editedIndex = -1;
          this.editedId = null;
          this.$refs.form?.reset();
        });
      },
  
      async save() {
        if (this.editedIndex > -1) {
          const { data } = await this.$http.put(`/api/v1/profits/${this.editedId}`, this.editedItem)
          data.client = this.clients.find(c => c.id === data.client)
          Object.assign(this.list[this.editedIndex], data);
          this.openAlert({status: true, text: 'Успешно изменено!'})
        } else {
          const { data } = await this.$http.post('/api/v1/profits/', this.editedItem)
          data.client = this.clients.find(c => c.id === data.client)
          this.list.unshift(data);
          this.openAlert({status: true, text: 'Успешно добавлено!'})
        }
        this.close();
      },
    },
  };
  </script>
  
  <style>
  </style>