<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="products"
      :search="search" :loading="loading"
      class="elevation-1"
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="12" sm="5" md="5">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                hide-details
                dense
                outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" md="4" class="d-flex justify-end">
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn block class="mb-2" v-bind="attrs" v-on="on" color="primary">
                    Добавить Клиента
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5 ml-3">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text class="pb-0">
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.name"
                              label="Имя клиента"
                              outlined
                              hide-details
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.description"
                              label="Описание"
                              outlined
                              hide-details
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.phone_number"
                              label="Телефонный номер"
                              outlined
                              hide-details
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.debt"
                              label="Долг"
                              outlined
                              hide-details
                              type="number"
                              min="0"
                              :rules="numberRules"
                            >
                            </v-text-field>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions class="px-7">
                      <v-spacer></v-spacer>
                      <v-btn color="primary" text @click="close">
                        Отмена
                      </v-btn>
                      <v-btn color="primary"
                        :disabled="!valid"
                        text @click="validate"
                      >
                        Сохранить
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="420px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="closeDelete"
                      >Отмена
                    </v-btn>
                    <v-btn color="primary" text @click="deleteItemConfirm"
                      >Удалить
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.status="{ item }">
        <v-chip small dark :color="item.status?'green darken-3':'red'">
          {{ item.status?'Зеленый':'Красный' }}
        </v-chip>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Изменить</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Удалить</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: "Users",
  data: () => ({
    search: "",
    products: [],
    dialog: false,
    dialogDelete: false,
    loading: false,
    headers: [
      { text: "Имя клиента", value: "name", sortable: false, align: "start" },
      { text: "Описание", value: "description" },
      { text: 'Телефонный номер', value: 'phone_number' },
      { text: "Долг", value: "debt" },
      { text: "Статус", value: "status" },
      // { text: "Дата", value: "date" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    editedIndex: -1,
    editedId: null,
    valid: false,
    image: null,
    editedItem: {
      name: "",
      description: "",
      status: true,
      phone_number: "",
      debt: 0,
    },
    defaultItem: {
      name: "",
      description: "",
      status: true,
      phone_number: "",
      debt: 0,
    },
    nameRules: [(v) => !!v || "Укажите поля"],
    numberRules: [(v) => !!v?.toString()?.trim() || "Укажите поля"],
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Добавить Клиента"
        : "Редактировать Клиента";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    ...mapMutations(['openAlert']),
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    async initialize() {
      this.loading = true
      const { data } = await this.$http.get('/api/v1/clients/')
      this.products = data;
      this.loading = false
    },

    editItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.editedId = item.id;
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.editedId = item.id;
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      this.dialogDelete = false;
      this.products.splice(this.editedIndex, 1);
      await this.$http.delete(`/api/v1/clients/${this.editedId}`)
      this.openAlert({status: true, text: 'Успешно удалено!'})
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    async save() {
      try {
        if (this.editedIndex > -1) {
          const { data } = await this.$http.put(`/api/v1/clients/${this.editedId}`, this.editedItem)
          Object.assign(this.products[this.editedIndex], data);
          this.openAlert({status: true, text: 'Успешно изменено!'})
        } else {
          const { data } = await this.$http.post('/api/v1/clients/', this.editedItem)
          this.products.push(data);
          this.openAlert({status: true, text: 'Успешно добавлено!'})
        }
        this.close();
      } catch (error) {
        console.log(error);
        this.openAlert({status: false, text: 'Ошибка!'})
      }
    },
  },
};
</script>

