import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        token: localStorage.getItem('token'),
        products: [],
        users: [],
        theme: JSON.parse(localStorage.getItem('dark')) || false,
        alertText: '',
        alertStatus: '',
        showAlert: ''
    },
    getters: {
        logined: state => !!state.token,
        dark: state => !!state.theme,
        alertText: state => state.alertText,
        alertStatus: state => state.alertStatus,
        showAlert: state => state.showAlert,
    },
    actions: {
    },
    mutations: {
        logout: state => {
            state.token = ''
            localStorage.removeItem('token')
        },
        settoken: (state, token) => {
            state.token = token
            localStorage.setItem('token', token)
        },
        settheme: (state) => {
            state.theme = !state.theme
            localStorage.setItem('dark', state.theme)
        },
        openAlert: (state, data) => {
            state.alertStatus = data.status
            state.alertText = data.text
            state.showAlert = true
        },
        closeAlert: (state) => {
            state.alertText = ''
            state.showAlert = false
        }
    }
})