import Vue from 'vue'
import Vuetify from 'vuetify/lib/framework'

Vue.use(Vuetify)

export default new Vuetify({
    theme: {
        dark: JSON.parse(localStorage.getItem('dark')) || false,
        themes: {
            dark: {
                primary: '#7C4DFF',
                // primary: '#536DFE',
            },
            light: {
                primary: '#00695C'
            }
        }
    }
})