<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="filteredItems" show-expand
      :expanded="expanded"
      class="elevation-1" :loading="loading"
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="12" sm="5" md="5">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                hide-details
                dense
                outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" md="4" class="d-flex justify-end">
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn block class="mb-2" v-bind="attrs" v-on="on" color="primary">
                    Добавить Расход
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5 pb-0 ml-3">Добавить Расход</span>
                    </v-card-title>

                    <v-card-text class="py-0">
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-select v-model="client"
                              label="Клиент" :items="clients"
                              outlined item-text="name" item-value="id"
                              hide-details
                              :rules="nameRules"
                            >
                            </v-select>
                          </v-col>
                          <v-row v-for="(op, i) in consumptions" :key="i" class="px-3" align="center">
                            <v-col cols="12" sm="4">
                              <v-select
                                v-model="op.product"
                                :label="`Товар (${getProductPrice(op.product)})`"
                                :items="products" 
                                item-text="name" item-value="id"
                                outlined @change="summ"
                                hide-details
                                :rules="nameRules"
                              >
                              </v-select>
                            </v-col>
                            <!-- :color="op.amount > getProductPrice(op.product) ? 'red' : 'primary'" -->
                            <v-col cols="5" sm="3">
                              <v-text-field
                                v-model="op.amount"
                                label="Количество товара"
                                outlined @input="summ"
                                hide-details
                                type="number"
                                min="0"
                                :rules="numberRulesLbL(getProductPrice(op.product))"
                              >
                              </v-text-field>
                            </v-col>
                            <v-col cols="5" sm="3">
                              <v-text-field
                                v-model="op.price"
                                label="Цена"
                                outlined @input="summ"
                                hide-details
                                type="number"
                                min="0"
                                :rules="numberRules"
                              >
                              </v-text-field>
                            </v-col>
                            <v-col cols="1">
                              <v-btn icon color="red" @click="deleteProduct(i)">
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </v-col>
                            <v-col cols="12" class="py-0">
                              <v-divider></v-divider>
                            </v-col>
                          </v-row>
                          <v-col cols="12">
                            <v-btn block height="45" color="primary" @click="addProduct">Добавить товар</v-btn>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions class="px-7 py-2">
                      <span>Стоимость: {{ total }}.$</span>
                      <v-spacer></v-spacer>
                      <v-btn text
                        color="primary"
                        :disabled="!valid"
                        @click="validate"
                      >
                        Сохранить
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="420px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="closeDelete"
                      >Отмена
                    </v-btn>
                    <v-btn color="primary" text @click="deleteItemConfirm"
                      >Удалить
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.client="{ item }"> 
        {{ item.client.name || 'Загрузка...' }}
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn small icon color="red" @click="deleteItem(item)">
          <v-icon small>mdi-delete</v-icon>
        </v-btn>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <CheckOutChildTable :consumption="item.consumptions"/>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import CheckOutChildTable from '@/components/CheckOutChildTable.vue'
import { mapMutations } from 'vuex'

export default {
  name: "CheckOut",
  data: () => ({
    search: "",
    list: [],
    expanded: [],
    dialog: false,
    dialogDelete: false,
    loading: false,
    headers: [
      { text: "Клиент", value: "client", sortable: false },
      { text: "Общая сумма", value: "total_cost" },
      { text: "Дата расхода", value: "created_at" },
      { text: "Действия", value: "actions", sortable: false },
      { text: '', value: 'data-table-expand' },
    ],
    editedIndex: -1,
    editedId: null,
    valid: false,
    client: {},
    consumptions: [],
    products: [],
    clients: [],
    total: 0,
    nameRules: [(v) => !!v || "Укажите поля"],
    numberRules: [(v) => !!v?.toString()?.trim() || "Укажите поля"],
  }),

  components: {
    CheckOutChildTable
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    filteredItems () {
      return this.list.filter(item => {
        return item.client.name.toLowerCase().includes(this.search.toLowerCase()) 
          || String(item.total_cost).toLowerCase().includes(this.search.toLowerCase())
          || String(item.created_at).toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  created() {
    this.initialize();
  },
  methods: {
    ...mapMutations(['openAlert']),
    numberRulesLbL(n){
      return [
        v => ((Number(v) < n) && !!v?.toString()?.trim()) || 'Укажите поля' 
      ]
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    sortClient(e){
      console.log(e);
    },
    async initialize() {
      this.loading = true
      const { data } = await this.$http.get('/api/v1/consumptions/')
      this.list = data
      this.loading = false
      // console.log(data);
      
      const products = await this.$http.get('/api/v1/products/')
      this.products = products.data

      const clients = await this.$http.get('/api/v1/clients/')
      this.clients = clients.data
    },

    getProductPrice(id){
      return this.products.find(el => el.id === id)?.balance || 0
    },

    summ(){
      this.total = this.consumptions.reduce((a, b) => {
        return Number(b.price) * Number(b.amount) + a
      }, 0)
    },

    deleteItem(item) {
      this.editedIndex = this.list.indexOf(item)
      this.editedId = item.id;
      this.dialogDelete = true;
    },

    addProduct(){
      this.consumptions.push({
        amount: 0,
        price: 0,
        product: 0
      })
    },

    deleteProduct(id){
      this.consumptions.splice(id, 1)
    },

    async deleteItemConfirm() {
      this.dialogDelete = false;
      this.list.splice(this.editedIndex, 1);
      await this.$http.delete(`/api/v1/consumptions/${this.editedId}`)
      this.openAlert({status: true, text: 'Успешно удалено!'})
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.consumptions.length = 0
        this.client = 0
        this.total = 0
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.consumptions.length = 0
        this.client = 0
        this.total = 0
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    async save() {
      try {
        const { data } = await this.$http.post('/api/v1/consumptions/', {
          client: this.client, 
          total_cost: this.total, 
          consumptions: this.consumptions
        })
        for(let index = 0; index < this.consumptions.length; index++){
          const item = this.products.find(p => p.id === this.consumptions[index].product)
          item.balance = item.balance - Number(this.consumptions[index].amount)
        }
        data.client = this.clients.find(c => c.id === data.client)
        for (let index = 0; index < data.consumptions.length; index++) {
          data.consumptions[index].product = this.products.find(p => p.id === data.consumptions[index].product)
        }
        this.list.unshift(data);
        this.openAlert({status: true, text: 'Успешно добавлено!'})
        this.close();
      } catch (err) {
        console.log(err);
        this.openAlert({status: false, text: 'Ошибка!'})
      }
    },
  },
};
</script>

