<template>
  <v-container fluid fill-height>
    <v-row justify="center">
      <v-card max-width="350" class="elevation-3">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-card-title>
            <span class="text-h5">Войдите в Личный кабинет</span>
          </v-card-title>

          <v-card-text>
            <v-row class="pt-2 login-page" justify="center">
              <v-col cols="12">
                <v-text-field
                  v-model="username"
                  label="Логин"
                  outlined
                  hide-details
                  :rules="nameRules"
                  prepend-inner-icon="mdi-account-outline"
                >
                </v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="password"
                  label="Пароль"
                  outlined
                  hide-details
                  :rules="nameRules"
                  prepend-inner-icon="mdi-lock-outline"
                  :append-icon="show ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
                  :type="show ? 'text' : 'password'"
                  @click:append="show = !show"
                >
                </v-text-field>
              </v-col>
              <v-col cols="12" class="mt-1">
                <v-btn color="primary"
                  :disabled="!valid" block
                  @click="validate" height="50">
                  Войти в систему
                </v-btn>
              </v-col>
            </v-row>
          </v-card-text>
        </v-form>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { mapMutations } from 'vuex'
import { configToken } from '@/main'

export default {
  name: "Login",
  data: () => ({
    username: "",
    password: "",
    show: false,
    valid: false,
    nameRules: [(v) => !!v || "Укажите поля"],
  }),
  methods: {
    ...mapMutations(['settoken', 'openAlert']),
    validate() {
      if (this.$refs.form.validate()) {
        this.register();
      }
    },
    async register() {
      try {
        const {data} = await this.$http.post('/auth/token/login/', { username: this.username, password: this.password })
        if(data?.auth_token) {
          this.settoken(data.auth_token)
          configToken()
          this.$router.push('/')
        this.openAlert({status: true, text: 'Успешный вход'})
        }
      } catch (error) {
        this.openAlert({status: false, text: 'Неверный логин или пароль'})
      }
    },
  },
};
</script>

<style>
.login-page .v-text-field--enclosed .v-input__prepend-inner{
    margin-top: 15px !important;
}
.login-page .v-application--is-ltr .v-text-field .v-input__prepend-inner{
    margin-top: 15px !important;
}
</style>