<template>
  <v-snackbar text :value="showAlert" @input="closeAlert"
    :timeout="2000" :color="alertStatus?'green':'red'">
    {{ alertText }}
    <template v-slot:action="{ attrs }">
      <v-btn icon :color="alertStatus?'green':'red'" text v-bind="attrs" @click="closeAlert">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
    computed: mapGetters(['alertStatus', 'alertText', 'showAlert']),
    methods: mapMutations(['openAlert', 'closeAlert'])
};
</script>

