    <v-dialog v-model="dialog" max-width="400px">
      <v-card>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-card-title>
            <span class="text-h5 ml-3">Добавить Приход</span>
          </v-card-title>

          <v-card-text class="pb-0">
            <v-container>
              <v-row align="center" v-for="(ad, i) in advents" :key="i">
                <v-col cols="6">
                  <v-select
                    v-model="ad.product"
                    label="Товар"
                    :items="products"
                    item-text="name"
                    outlined
                    item-value="id"
                    hide-details
                    :rules="numberRules"
                  >
                  </v-select>
                </v-col>
                <v-col cols="4">
                  <v-text-field
                    v-model="ad.amount"
                    label="Количество товара"
                    outlined
                    hide-details
