</template>
  
<script>
import CheckInChildTable from '@/components/CheckInChildTable.vue'
import { mapMutations } from 'vuex'

export default {
  name: "CheckIn",
  data: () => ({
    search: "",
    products: [],
    expanded: [],
    list: [],
    dialog: false,
    dialogDelete: false,
    editedIndex: -1,
    editedId: null,
    loading: false,
    valid: false,
    headers: [
      { text: "Дата расхода", align: 'start', value: "created_at" },
      { text: "Действия", value: "actions", align: 'end', sortable: false },
      { text: '', value: 'data-table-expand' },
    ],
    advents: [
      {
        amount: 0,
        product: 0,
      },
    ],
    nameRules: [(v) => !!v || "Укажите поля"],
    numberRules: [(v) => !!v?.toString()?.trim() || "Укажите поля"],
    page: 1,
    itemsPerPage: 5,
  }),
  computed: {
    numberOfPages() {
      return Math.ceil(this.list.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
  components: {
    CheckInChildTable
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    ...mapMutations(['openAlert']),
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    async initialize() {
      this.loading = true
      const { data } = await this.$http.get("/api/v1/advents/");
      this.list = data;
      this.loading = false
      // console.log(data);
      const product = await this.$http.get("/api/v1/products/");
      this.products = product.data;
    },

    getClient(i) {
      return this.clients.find((el) => el.id === i);
    },

    deleteItem(item) {
      this.editedIndex = this.list.indexOf(item);
      this.editedId = item.id;
      this.dialogDelete = true;
    },

    getProduct(i) {
      return this.products.find((el) => el.id === i);
    },

    async deleteItemConfirm() {
      this.dialogDelete = false;
      this.list.splice(this.editedIndex, 1);
      await this.$http.delete(`/api/v1/advents/${this.editedId}`);
      this.openAlert({status: true, text: 'Успешно удалено!'})
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.advents.length = 0;
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    addProduct() {
      this.advents.push({
        amount: 0,
        product: 0,
      });
    },

    deleteProduct(i) {
      this.advents.splice(i, 1);
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.advents.length = 0;
        this.editedIndex = -1;
        this.editedId = null;
        this.$refs.form?.reset();
      });
    },

    async save() {
      const { data } = await this.$http.post("/api/v1/advents/", {
        advents: this.advents.map((a) => ({
          amount: Number(a.amount),
          product: a.product,
        })),
      });
      for (let index = 0; index < data.advents.length; index++) {
        data.advents[index].product = this.getProduct(data.advents[index].product)
      }
      this.list.unshift(data);
      this.openAlert({status: true, text: 'Успешно добавлено!'})
      this.close();
    },
  },
};
</script>
  
